body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#baap{
   background: url("corona.gif");
    background-size: cover;
    background-position: center;
    height: 100vh;
    margin-top: 0px;
    position: absolute;
    top: 0px;
    width: 100%;
 
}

.outter{
  border:2px solid black;
  width:50%;
  margin: auto;
  height: 500px;
  background-color:#00000099  ;
  margin-top: 8%;
  margin-bottom: 8%;
  border:4px solid #00FF89;
  border-radius: 20px;
   overflow-y: hidden!important;

   
}

.inner{
  color: white;
 
}

.outter input:focus{
  outline: none;
}
.outter input{
  width: 50%;
  height: 50px;
  margin-top:70px;
   background-color: #00000099;
   text-align: center;
   color:white;
   border:2px solid white;
   font-weight: bolder;
   font-size: 29px;
  border-radius: 100px;
   transition: 0.4s;
}

.outter input:hover{
  width: 70%;
  border-radius: 20px;
 
}

.inner {
  margin-top: 25px;
}
.date{
  color:#00FF89!important;
  font-weight: bolder;
  font-size: 29px;

}

.inner .p {
  color: #00FF89;
  font-weight: bolder;
  font-size: 21px;
  border-bottom: 2px solid #00FF89;
  width: 80%;
  margin: auto;
  padding-top: 10px;
  padding-bottom: 10px;
   text-align: left;

}
.inner span{
color:white;
}
.inner .p1{
  color: #00FF89;
  font-weight: bolder;
  font-size: 21px;
  border-top: 2px solid #00FF89;
  border-bottom: 2px solid #00FF89;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 80%;
  margin: auto;
  text-align: left;

}

.fa-calendar-check-o{
  color:white;
  
}
.fa-certificate{
  color:orange;
  text-align: left;
  font-size: 29px;
}
.fa-child{
  color: lime;
  font-size: 34px;
}
.fa-user-times{
  color:red;
  font-size: 22px;
}

.fa-map-marker{
  color: tomato;
  font-size: 27px;
}

.fa-info-circle{
  color:#00FF89;
}

.st-1{
  font-size: 21px;
font-weight: bolder;
padding-bottom: 5px;
width: 80%;
margin: auto;
margin-top: 25px;
text-transform: uppercase;
}
.st{
font-size: 21px;
font-weight: bolder;
padding-bottom: 20px;
width: 80%;
margin: auto;
text-transform: uppercase;
}

.tt{
  font-size: 21px;
font-weight: bolder;
padding-bottom: 20px;
width: 80%;
margin: auto;
text-transform: uppercase;
}






.upper-d{
   
position: relative;
top:40px;
width: 90%;
margin: auto;
}
.d-k{
  color: white;
  text-transform: capitalize;
}
.b-k{
margin-left: 10px;
border: 2px solid #00FF89;
border-radius: 20px!important;
font-weight: 700;
color: #00FF89;
transition: 0.2s;
background-color: black;
padding:4px 7px 4px 7px;
text-transform: capitalize;
}

.b-k:hover{
   background-color: #00FF89;
   color: black;
   cursor: pointer;
}


/* //state-code-list-component */

.state-code{
  color: white;
}

h1{
  text-transform: uppercase;
  font-weight: bolder;

}

.state-div{
  background-color: #00000099;
padding-top: 20px;
padding-bottom: 20px;
  width: 70%;
  margin: auto;
}

table{
  margin-top: 40px!important;
  color:white;
  margin: auto;
  text-transform: uppercase;
  
  
}

thead {
  background-color: #00FF89;
  
}

th{
  width: 450px;
  color: #000000;
  font-weight: 700;
  
}

tr td{
  border-bottom: 2px solid #00FF89;
  padding-top: 5px ;
  padding-bottom: 5px;
  
}


.btn-h{

border: 2px solid #00FF89;
border-radius: 20px!important;
font-weight: 700;
color: #00FF89;
transition: 0.2s;
background-color: black;
padding:7px 11px 7px 11px;
text-transform: capitalize;
}

.btn-h:hover{
   background-color: #00FF89;
   color: black;
   cursor: pointer;
}

button:focus{
  outline: none;
}

#copyright{
  background-color: black;
  width: 100%;
  color:#00FF89;


}

#copyright p{
  padding: 5px 5px;
}


